.projectDiv {
  height: 40%;
  width: 40%;
  margin: 2.5%;
  border: 2px solid grey;
  padding: 2%;

}
.projectDiv .projectText {
  padding-top: 2%;
  color: white;
  display: none;
}

.projectDiv:hover .projectText {
  color: white;
  display: block;
}

.project {
  display: flex;
  background-color: #181a1f;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap
}

.projectTitle {
  color: white;;
}

.projectImage {
  width: 100%;
}

.linkStyles {
  text-decoration: none;
  color: white;
}

@media only screen and (max-width: 800px) {
  .project {
    display: flex;
    background-color: #181a1f;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap
  }
  .projectDiv {
    height: 30%;
    width: 77%;
    margin: 4.5%;
  }
}

@media only screen and (min-width: 900px) {
  .projectDiv .projectImage {
    width: 100%;
    color: white;;
    opacity:0.6;
    transition: .5s ease;
  }
    
  .projectDiv:hover .projectImage{
    opacity:1;
    transition: .5s ease;
  }

  .projectDiv .projectText {
    padding-top: 2%;
    color: white;
  }
  
  .projectDiv:hover .projectText {
    color: white;
    padding-top: 2%;
  }
}